


















import { defineComponent, onMounted } from "@vue/composition-api";
import { vibrate } from "@/use/common";
export default defineComponent({
  name: "TheAlert",
  props: {
    alertType: {
      type: String,
      required: true,
    },
    alertText: {
      type: String,
      required: true,
    },
  },
  setup(_, ctx) {
    const store = ctx.root.$store;

    onMounted(() => vibrate());

    const setError = () => store.dispatch("setError");

    const onClose = () => {
      setError();
      ctx.emit("dismissed");
    };

    return {
      onClose,
    };
  },
});
