

































































import { defineComponent, ref } from '@vue/composition-api'

export default defineComponent({
  name: 'BaseDialog',
  props: {
    activatorIcon: {
      type: String,
      default: ''
    },
    header: {
      type: String,
      required: true
    },
    color: {
      type: String,
      default: 'secondary'
    },
    activatorText: {
      type: String,
      default: ''
    },
    simple: {
      type: Boolean
    }
  },
  setup(_, ctx) {
    const dialog = ref(false)
    const valid = ref(false)

    const close = () => {
      dialog.value = false
    }
    const onSubmit = () => {
      ctx.emit('submitLogic')
      close()
    }

    return { dialog, valid, onSubmit, close }
  }
})
